@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
    max-width: 100%;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Post {
    position: relative;
    margin: 0 auto;
    background: #15202b;
    width: 856px;
    min-height: 500px;
    border-radius: 5px;
    font-family: 'Open Sans', sans-serif;
    color: #fff;
    display: -webkit-flex;
    display: flex;
    padding: 20px 0px 5px 20px;
}

.avatar {
    position: relative;
    width: 100px;
    height: 100px;
    background: #292929;
    border-radius: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 50px;
    border: 2px solid #8798a5;
}

.avatar img {
    max-height: 100%;
    max-width: 100%;
    margin: 0 auto;
    border-radius: 50px;
}

.content {
    position: relative;
    display: block;
    top: 20px;
    max-width: 80%;
}

.head {
    position: relative;
    -webkit-align-items: center;
            align-items: center;
}

.post_colum_1 {
    display: -webkit-flex;
    display: flex;
    height: 30px;
    margin-bottom: 10px;
}

.name {
    position: relative;
    font-size: 20px;
    font-weight: 600;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    margin-right: 5px;
}

.nickname {
    color: #8798a5;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    margin-right: 5px;
}

.today_date {
    color: #8798a5;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    margin-right: 5px;
}

.today_date::before {
    content: "\00B7";
    margin-right: 5px;
}

.arrow {
    position: relative;
    margin-left: auto;
}

.post_text {
    position: relative;
    max-height: 150px;
    overflow: hidden;
    -webkit-box-orient: vertical;
    display: block;
    display: -webkit-box;
    overflow: hidden !important;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    margin-bottom: 20px;
}

.content_img {
    position: relative;
    min-width: 650px;
    height: 310px;
    background: #292929;
    border-radius: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 20px;
    text-align: -webkit-center;
    border: 2px solid #8798a5;
}

.content_img img {
    margin: 0 auto;
    height: 100%;
    width: 100%;
    border-radius: 20px;
}

.post_stats {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 0px 30px;
    width: 75%;
}

.comment_stats::before {
    font-family: 'icomoon';
    content: "\f0e5";
    margin-right: 5px;
}

.shares_stats::before {
    font-family: 'icomoon';
    content: "\ea2d";
    margin-right: 5px;
}

.likes_stats::before {
    font-family: 'icomoon';
    content: "\f08a";
    margin-right: 5px;
}

.share::before {
    font-family: 'icomoon';
    content: "\ea7d";
    margin-right: 5px;
}
