    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-heart-o:before {
    content: "\f08a";
}

.icon-comment-o:before {
    content: "\f0e5";
}

.icon-angle-down:before {
    content: "\f107";
}

.icon-loop:before {
    content: "\ea2d";
}

.icon-share:before {
    content: "\ea7d";
}
